import React, { useState } from 'react';
import axios from 'axios';

import 'bootstrap/dist/css/bootstrap.min.css';

import { Button, Table, Image } from 'react-bootstrap';

const App = () => {
	const [users, setUsers] = useState([])
	const [books, setBooks] = useState([])
	const [, setPhotos] = useState([])
	const [avatar, setAvatar] = useState('')

	const fetch = async () => {
		const url = 'http://source.unsplash.com/random/500x500';
		const cors = 'https://cors-anywhere.herokuapp.com/'

		const [resUsers, resPhotos, resAvatar] = await Promise.all([
			axios.get(`https://jsonplaceholder.typicode.com/users`),
			axios.get(`https://jsonplaceholder.typicode.com/photos`),
			axios.get('https://dog.ceo/api/breeds/image/random')
		])

		getBook();
		setUsers(resUsers.data)
		setPhotos(resPhotos.data)
		setAvatar(resAvatar.data['message'])
	}

	const getBook = async () => {
		const books = await axios.get('http://localhost:1002/book')
		setBooks(books.data)
	}

	return (
		<div className="App">
			<div className="container">
				<div className="row mb-3 mt-3">
					<div className="m-auto">
						<Button variant="outline-primary" onClick={fetch}>Get Data</Button>
					</div>
				</div>
				{books ? <div className="row">
					<Table striped bordered hover>
						<thead>
							<tr>
								<th>#</th>
								<th>Title</th>
								<th>Author</th>
								<th>Published Date</th>
								<th>Average Rating</th>
								<th>Page Count</th>
								<th>Link Preview</th>
								<th>Thumbnail</th>
							</tr>
						</thead>
						<tbody>
							{books.map((b, i) => (
								<tr key={b.id}>
									<td>{i + 1}</td>
									<td>{b.volumeInfo['title']}</td>
									<td>{b.volumeInfo['authors'][0]}</td>
									<td>{b.volumeInfo['publishedDate']}</td>
									<td>{b.volumeInfo['averageRating']}</td>
									<td>{b.volumeInfo['pageCount']}</td>
									<td>
										<a href={b.volumeInfo['previewLink']} target="_blank" rel="noopener noreferrer">Link Preview</a>
									</td>
									<td>										
										{b.volumeInfo.hasOwnProperty('imageLinks') ?
										<Image src={b.volumeInfo['imageLinks']['thumbnail']} thumbnail /> : null}
									</td>
								</tr>
							))}
						</tbody>
					</Table>
				</div> : null}
				<hr />
				{users ? <div className="row">
					<Table striped bordered hover>
						<thead>
							<tr>
								<th>#</th>
								<th>FullName</th>
								<th>Email</th>
								<th>Phone</th>
								<th>Website</th>
								<th>Avatar</th>
							</tr>
						</thead>
						<tbody>
							{users.map(u => (
								<tr key={u.id}>
									<td>{u.id}</td>
									<td>{u.name}</td>
									<td>{u.email}</td>
									<td>{u.phone}</td>
									<td>{u.website}</td>
									<td>
										<img src={avatar} alt="Avatar" width="200" height="200" />
									</td>
								</tr>
							))}
						</tbody>
					</Table>
				</div> : null }
			</div>
		</div>
	);
}

export default App;
